import { defineComponent, ref, computed } from "vue";
import { useStore } from "@/store";
import PageContent from "@/components/page-content";
import PageSearch from "@/components/page-search";
import { contentTableConfig } from "./config/content.config";
import { searchFormConfig } from "./config/search.config";
import { usePageSearch } from "@/hooks/use-page-search";
import { modalConfig } from "./config/model.config";
import PageModal from "@/components/page-model";
import { usePageModal } from "@/hooks/use-page-modal";
export default defineComponent({
  name: "user",
  components: {
    PageSearch,
    PageContent,
    PageModal
  },

  setup() {
    const [pageContentRef, handleResetClick, handleQueryClick] = usePageSearch();
    let dialogVisible = ref(false); // const handleNewData = () => {
    //   dialogVisible.value = true
    // }
    // const handleEditData = (item: any) => {
    //   dialogVisible.value = true
    // }
    //处理密码的逻辑

    const newCallback = () => {
      const passwordItem = modalConfig.formItems.find(item => item.field === "password");
      passwordItem.isHidden = false;
    };

    const editCallback = () => {
      const passwordItem = modalConfig.formItems.find(item => item.field === "password");
      passwordItem.isHidden = true;
    }; //动态添加部门和角色列表


    const store = useStore();
    const modalConfigRef = computed(() => {
      const departmentItem = modalConfig.formItems.find(item => item.field === "departmentId");
      departmentItem.options = store.state.entireDepartment.map(item => {
        return {
          title: item.name,
          value: item.id
        };
      });
      const roleItem = modalConfig.formItems.find(item => item.field === "roleId");
      roleItem.options = store.state.entireRole.map(item => {
        return {
          title: item.name,
          value: item.id
        };
      });
      return modalConfig;
    });
    const [pageModalRef, defaultInfo, handleNewData, handleEditData] = usePageModal(newCallback, editCallback);
    return {
      searchFormConfig,
      contentTableConfig,
      pageContentRef,
      handleResetClick,
      handleQueryClick,
      dialogVisible,
      modalConfig,
      handleNewData,
      handleEditData,
      pageModalRef,
      defaultInfo,
      modalConfigRef
    };
  }

});